.overlay {
	background: rgba(0, 0, 0, 0.7);
	height: 100vh;
	margin: -80px 0;
	padding: 0;
	position: fixed;
	width: 100vw;
	z-index: 20;
}
.overlay-inner {
	background: -webkit-gradient(linear, right bottom, left top, from(#000), to(#111));
	background: -webkit-linear-gradient(bottom right, #000, #111);
	background: -o-linear-gradient(bottom right, #000, #111);
	background: linear-gradient(to top left, #000, #111);
	-webkit-box-shadow: 0px 0px 4px 1px #000;
	        box-shadow: 0px 0px 4px 1px #000;
	color: white;
	font-family: 'open sans';
	font-size: 110%;
	position: absolute;
	top: 20%;
	bottom: 20%;
	left: 20%;
	right: 20%;
	verflow: auto;
}
.backdrop {
	background-position: center;
	background-size: cover;
	-webkit-box-shadow: inset 0px 0px 150px 20px #111;
	        box-shadow: inset 0px 0px 150px 20px #111;
	height: 50%;
	margin-top: 0px;
	padding: 20px 0;
	position: relative;
	text-align: left;
	text-shadow: 0px 0px 40px black;
	width: 100%;
}
.backdrop-filter {
	background: -webkit-gradient(linear, left top, right top, from(rgba(0, 0, 0, 0.7)), color-stop(60%), to(rgba(0, 0, 0, 0)));
	background: -webkit-linear-gradient(left, rgba(0, 0, 0, 0.7), 60%, rgba(0, 0, 0, 0));
	background: -o-linear-gradient(left, rgba(0, 0, 0, 0.7), 60%, rgba(0, 0, 0, 0));
	background: linear-gradient(to right, rgba(0, 0, 0, 0.7), 60%, rgba(0, 0, 0, 0));
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
}
.overlay-title {
	margin-bottom: 8px;
	padding-top: 10px;
	padding-left: 40px;
	width: 48%;
}
.overlay-tagline {
	margin-bottom: 2px;
	font-style: oblique;	
	padding: 0px;
	padding-left: 40px;
	width: 48%;
}
.overlay-runtime {
	display: -ms-inline-flexbox;
	display: inline-flex;
	padding-left: 40px;
}
.overlay-text {
	overflow: auto;
	padding: 0px;
	padding-left: 40px;
	width: 70%;
}
.overlay-img-container {
	height: 42%;
	padding: 2px;
	overflow: auto;
}
.overlay-imgs {
	background-position: center;
	background-size: cover;
	display: -ms-inline-flexbox;
	display: inline-flex;
	height: 160px;
	margin: 0px 2px;
	width: 240px;
}


.fa-times-circle {
	font-size: 30px;
	position: absolute;
	top: 2%;
	right: 1%;
	-webkit-transition: all 0.2s ease-out;
	-o-transition: all 0.2s ease-out;
	transition: all 0.2s ease-out;
}
.fa-times-circle:hover {
	color: red;
}

@media(max-width: 1000px) {
	.overlay {
		background: rgba(0, 0, 0, 0.7);
		margin: 0;
		padding: 0;
		position: fixed;
		top: 0;
		z-index: 20;
	}
	.overlay-inner {
		font-size: 100%;
		top: 0%;
		bottom: 0%;
		left: 0%;
		right: 3%;
	}
	.overlay-title {
		padding: 0 30px;
		padding-top: 10px;
		width: auto;
	}
	.overlay-tagline {	
		padding: 0 30px;
		width: auto;
	}
	.overlay-text {
		max-height: 50%;
		padding: 10px 30px;
		width: auto;
	}
	.backdrop {
		height: 50%;
	}	
	}



